.cuaca__info {
  width: 100%;
  display: flex;
  justify-content: center;
  align-items: center;
  flex-direction: column;
}
.cuaca__info .cuaca__icon {
  display: flex;
  justify-content: center;
  align-items: center;
  height: 200px;
  width: 200px;
  padding: 20px;
  background-color: #9dc8d7;
  border-radius: 50%;
  transition: all 0.5s ease;
  animation: bounce 3s infinite ease-in-out;
}
@media screen and (min-width: 64rem) {
  .cuaca__info .cuaca__icon {
    height: 150px;
    width: 150px;
  }
}
@media screen and (min-width: 87.5rem) {
  .cuaca__info .cuaca__icon {
    height: 200px;
    width: 200px;
  }
}
@keyframes bounce {
  0% {
    transform: translateY(-2px);
  }
  50% {
    transform: translateY(5px);
  }
  100% {
    transform: translateY(-2px);
  }
}
.cuaca__info .cuaca__icon img {
  width: 100%;
  transition: all 0.5s ease;
}
.cuaca__info .cuaca__icon:hover {
  background-color: #96bfce;
  box-shadow: 0px 0px 20px rgba(0, 0, 0, 0.3);
}
.cuaca__info .cuaca__info__container {
  background-color: #9dc8d7;
  display: flex;
  flex-direction: column;
  align-items: center;
  margin-top: 2rem;
  width: 90%;
  height: 380px;
  border-radius: 2.5rem;
  transition: 0.5s all ease;
}
@media screen and (min-width: 64rem) {
  .cuaca__info .cuaca__info__container {
    border-radius: 5rem;
    width: 90%;
    height: 100%;
    margin-top: 1rem;
  }
}
@media screen and (min-width: 87.5rem) {
  .cuaca__info .cuaca__info__container {
    width: 90%;
    height: 380px;
    margin-top: 2rem;
  }
}
.cuaca__info .cuaca__info__container h2 {
  margin-top: 2rem;
  color: #eaf0f5;
  font-weight: 400;
  border-bottom: 1px #eaf0f5 solid;
}
.cuaca__info .cuaca__info__container p {
  color: #eaf0f5;
  margin-top: 1rem;
  font-size: 4rem;
  font-weight: 300;
  transition: 0.5s all ease;
  margin-bottom: 0;
}
.cuaca__info .cuaca__info__container .cuaca__info__nama {
  font-size: 2rem;
  margin: 0 0 1rem 0;
}
.cuaca__info .cuaca__info__container .cuaca__info__keterangan {
  display: grid;
  grid-template-columns: 1fr 1fr;
  width: 100%;
}
.cuaca__info .cuaca__info__container .cuaca__info__keterangan h2 {
  font-size: 1rem;
  margin-top: 0rem;
  color: #eaf0f5;
  font-weight: 400;
  border-bottom: 1px #eaf0f5 solid;
}
.cuaca__info .cuaca__info__container .cuaca__info__keterangan p {
  color: #eaf0f5;
  margin-top: 0.5rem;
  margin-bottom: 0.5rem;
  font-size: 1rem;
  font-weight: 300;
  transition: 0.5s all ease;
}
.cuaca__info .cuaca__info__container .cuaca__info__keterangan .keterangan {
  margin-top: 0.5rem;
  margin-bottom: 1rem;
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
}
.cuaca__info .cuaca__info__container:hover {
  transform: translateY(-2px);
  box-shadow: 0px 0px 20px rgba(0, 0, 0, 0.3);
}